import React from 'react';
import PropTypes from 'prop-types';

const Cell = ({ data }) => (
  <div className="cell-container">
    <article className="mini-post">
      <object data={data.pdf} type="application/pdf" width="100%" height="1310px">
        <iframe title={data.title} src={data.google_pdf} width="100%" height="1310px">resume</iframe>
      </object>
    </article>
  </div>
);

Cell.propTypes = {
  data: PropTypes.shape({
    title: PropTypes.string,
    pdf: PropTypes.string,
    google_pdf: PropTypes.string,
  }).isRequired,
};

export default Cell;
